*{
  margin: 0%;
  padding: 0%;
}
body{
  background-color: blueviolet;
  font-family: Arial, Helvetica, sans-serif;
}
.weather-box{
    width: 500px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    box-shadow:0px 20px 20px rgba(0,0,0,0.5);
    text-align: center;
  
}

.weather-header h1{
  color: white;
  margin: 15px;

}
.weather-search{
  border: 2px solid;
  border-radius: 30px;
  outline: none;
  margin: 10px;
}

.weather-city{
  color:white;
  margin: 20px;
}
.weather-row {
  display: flex;
  flex-direction: row;
  color: white;
  margin: 10px;
  padding: auto;
}

.weather-column {
  flex-basis: 50%;
}
